// DEMO PAGE

body {
  text-size-adjust: 100%;
  background-color: #00b76f;
  font: 100%/1.5 sans-serif;
  -webkit-font-smoothing: antialiased;
  padding: 0;
  margin: 0; }

.page {
  font-size: 90%;
  color: #FFF;
  padding: 2em 4em;

  code {
    font-size: 130%; }
  li {
    margin: .25em 0; }
  button {
    position: relative;
    z-index: 12;
    border: 0 none;
    border-radius: 2px;
    background: rgba(0,0,0,0.42);
    color: white;
    padding: .5em;
    cursor: pointer;
    &:hover {
      color: white; }
    &:disabled {
      background-color: transparent; } } }
